export default {
 
  data () {
    return {
      list: [],
      number: 0,
      isFinish: false
    }
  },
  onLoad () {
    uni.showLoading({
        title: '加载中',
    	mask:"true"
    });
	this.getList()
  },
  methods: {
	  FreeDetail(item){
		 uni.navigateTo({
	        url:'../../pagesThree/EveryMonthDetail/EveryMonthDetail?start='+item.startDate+"&end="+item.endDate
		 }) 
	  },
	  getList(){
		  // console.log(123)
		  this.$api.month_reg_count({
			  type:2
		  }).then(res=>{
		  	uni.hideLoading();
			// console.log(res)
			if(res.data.code=="200"){
				this.number = res.data.data.totalCount
				this.list = res.data.data.list
				this.isFinish = true
			}
		  })
		  
	  }
  }
}
